@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding: 0;
  margin: 0;
  background-color: #EE817C;
  background-image: url(/reactapp_A2/static/media/bg-intro-desktop.059183ee.png);
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 10%;
  width: 100vw;
}

.content {
  margin-top: 6%;
}

.content h1 {
  color: white;
  font-size: 50px;
  font-family: poppins;
  font-weight: 500;
}

.content p {
  color: #FCE8E6;   
  line-height: 30px;
  font-size: 20px;
}

form {
  background-color: white;
  border-radius: 10px;
  width: 500px;
  height: 410px;
  padding: 20px;
}

.box {
  margin-left: 5%;
}

.first {
  color: white;
  background-color: #5D549E;
  border: none;
  margin-bottom: 15px;
  width: 540px;
  height: 50px;
  border-radius: 10px;
}

.second {
  color: white;
  background-color: #69C98F;
  border: none;
  width: 480px;
  height: 50px;
  border-radius: 10px;
  margin-left: 12px;
}

.box h6 {
  color: #D0CED9;
}

input {
  width: 470px;
  margin: 15px;
  height: 45px;
  border-radius: 5px;
  padding-left: 10px;
  border: 1px solid #F0EFEF;
}

h6 {
  margin: 0;
  -webkit-align-content: center;
          align-content: center;
  margin-left: 90px;
  margin-top: 10px;
}

.pink {
  color: #EE817C;
  font-size: 9px;
}

.bold {
  color: white;
}

.nobold {
  color: #CEC8E6;
}
